    getCurrentDatetime(){

    	return moment().format('YYYY-MM-DD HH:mm:ss');
    }

    gerHours(t){

    	// return Math.floor(min/60);
    	return  Math.floor((t / 60 ) / 60);
    }

    getMinutes(t){
    	
    	// return Math.floor(min % 60); 
    	return Math.floor((t / 60 ) % 60); 

    }
    getSeconds(t){
        return Math.floor(t % 60);
    }

