import { Component, OnInit, Input  } from '@angular/core';
import {Router, ActivatedRoute} from '@angular/router';
import {HttpClient, HttpHeaders } from '@angular/common/http';
import {FormBuilder, FormGroup, Validators} from "@angular/forms";
import { ToastrManager } from 'ng6-toastr-notifications';

@Component({
  selector: 'app-task',
  templateUrl: './task.component.html',
  styleUrls: ['./task.component.css']
})
export class TaskComponent implements OnInit {
  
  project:any;
  addFormTask: FormGroup;
  projectID:number;
  editFormTask: FormGroup;
  status: any;


  constructor(public router: Router,public toastr: ToastrManager,private route:ActivatedRoute,private http: HttpClient,private formBuilder: FormBuilder){}

  ngOnInit() {
  	// if (this._countdown.functionToAnotherCompenent()) {console.log(this._countdown.functionToAnotherCompenent())}
  
    this.projectID = this.route.snapshot.params['id'];

    this.project = this.getProjectByID(this.projectID);
    this.project.tasks=this.getProjectTasks(this.projectID);
    // console.log(this.project);
    this.addFormTask = this.formBuilder.group({
             name: [''],
             duration: [''],
             id_project: this.projectID,
             timer_status: 0,
             start_task: null,
             end_task: null,
             timer_seconds: 0
    });

    this.editFormTask = this.formBuilder.group({
    	     id:[''],
             name: [''],
             duration: [''],
             id_project: this.projectID,
             timer_seconds:[''],
             start_task:[''],
             end_task:['']
    });
    
  }

  getProjectTasks(id){

      let datas=JSON.parse(localStorage.getItem("taskStorage"));

      let tasks=datas.filter(task => task.id_project === id);
      // console.log(tasks);
      return tasks;
  }

  getProjectByID(id){

      let datas=JSON.parse(localStorage.getItem("projectStorage"));

      let project=datas.filter(project => project.id === id);
      // console.log('dsads');
      // console.log(project[0]);
      return project[0];
  }

  showToast(msg,status) {

        if (status == "success") {
          this.toastr.successToastr(msg, 'Success', {
                 position: 'top-center',
                 animate: 'slideFromTop',
                 toastTimeout:2000
            });
        }

        if (status == "error") {
          this.toastr.errorToastr(msg, 'Error', {
                 position: 'top-center',
                 animate: 'slideFromTop'
            });
        }
    }

    onSubmit() {
        // console.log(this.addFormProject.value);
        let data = this.addFormTask.value;
        data.id=this.idGenerator();
        let taskStorage=JSON.parse(localStorage.getItem("taskStorage"));
        taskStorage.push(data);
        // console.log(projectStorage);
        localStorage.setItem("taskStorage", JSON.stringify(taskStorage)); 
        if (data) {
          this.showToast("Task Successfully Saved","success");
          this.project.tasks.push(data)
        }else{
          this.showToast("Something went wrong ","error");
        }

          this.addFormTask.reset({ id_project: this.projectID,
                                   timer_status: 0,
                                   start_task: null,
                                   end_task: null,
                                   timer_seconds: 0 });
    }

    deleteTask(task){

      let taskStorage=JSON.parse(localStorage.getItem("taskStorage"));
      taskStorage.splice(taskStorage.findIndex(function(i){
                    return i.id === task.id;
      }), 1);
      localStorage.setItem("taskStorage", JSON.stringify(taskStorage)); 
      this.project.tasks = this.project.tasks.filter(u => u !== task);
      this.showToast("Task Successfully deleted","success");

    }

    getEditTask(task){
     console.log(task);

    	this.editFormTask.setValue({
    		 id:task.id,
             name:task.name,
             duration: task.duration,
             id_project: this.projectID,            
             timer_seconds: task.timer_seconds,
             start_task: task.start_task,
             end_task: task.end_task
        });

    }

    updateTask(){ //update storage
        
        let taskStorage=JSON.parse(localStorage.getItem("taskStorage"));
        taskStorage.splice(taskStorage.findIndex((i) => {
                    return i.id === this.editFormTask.value.id;
                    // console.log(this.editFormTask.value.id);
                    // console.log('update storage');
                    // console.log(i.id);
               }), 1);

        const index = this.project.tasks.findIndex(e => e.id === this.editFormTask.value.id);
        this.project.tasks[index] = this.editFormTask.value;  
        taskStorage.push(this.editFormTask.value);
        localStorage.setItem("taskStorage", JSON.stringify(taskStorage)); 
        this.showToast("Task Successfully Updated","success");

    }

    handleStatusTask(worked,duration){

    	if (Number(worked) == 0) {
    		return "<i class='fas fa-check-circle false-status' title='Not started'></i>";
    	}

    	if (Number(worked) > 0 && Number(worked) < Number(duration))  {
    		return "<i class='fa fa-spinner fa-spin progress-status' title='In Progress'></i>";
    	}

    	if (Number(worked) === Number(duration)) {
    		return "<i class='fas fa-check-circle true-status' title='Completed'></i>";
    	}
    	
    	// this.status="<i class='fas fa-check-circle' style='font-size:20px;color: #DCE0E3'></i>";

    }

    handleStatusTask2(task){
         
        let toSeconds=Number(task.duration)*60*60;
    	if (Number(task.timer_seconds) == 0) { 
    		return "<i class='fas fa-check-circle false-status' title='Not started'></i>";
    	}

    	if (Number(task.timer_seconds) > 0 && Number(task.timer_seconds) < Number(toSeconds))  { 
    		return "<i class='fa fa-spinner fa-spin progress-status' title='In Progress'></i>";
    	}

    	if (Number(task.timer_seconds) === Number(toSeconds)) { 
    		return "<i class='fas fa-check-circle true-status' title='Completed'></i>";
    	}
    	
    	// this.status="<i class='fas fa-check-circle' style='font-size:20px;color: #DCE0E3'></i>";

    }

    idGenerator() {

       let S4 = function() {
                return (((1+Math.random())*0x10000)|0).toString(16).substring(1);
             };
       return (S4()+S4()+"-"+S4()+"-"+S4()+"-"+S4()+"-"+S4()+S4()+S4());
    }

}

